import Vue from 'vue'
function mutationLoop (context, mutation, data) {
  for (var i=0; i<data.length; i++) {
    context.commit(mutation, data[i])
  }
}
const defaultState = {
  slaves_map: {},
  slave_types: {}
}

const getters = {
  slave_active (state, getters) {
    return getters.slaves.filter(function(slave) {
      return slave['active']
    })
  },

  slaves: (state) => {
    return Object.keys(state.slaves_map).map(slave_id => state.slaves_map[slave_id])
  }
}
const actions = {

  SET_SLAVE: (context,  data) => {
    context.commit("SET_SLAVE",data)
  },
  SET_SLAVES: (context, data) => {
    mutationLoop(context, 'SET_SLAVE', data)
  },
  SET_SLAVE_TYPES: (context, data) => {
    mutationLoop(context, 'SET_SLAVE_TYPE', data)
  },
  SET_SLAVE_TYPE: (context, data) => {
    context.commit("SET_SLAVE_TYPE", data)
  },
  RESET_BOT_STORE: (context, data) => {
    context.commit("RESET_BOT_STORE", data)
  }
}

const mutations = {
  RESET_BOT_STORE (state, data) {
    state.slaves_map = {} 
    state.slave_types = {}
  },
  SET_SLAVE_TYPE (state, data) {
    var keys = Object.keys(data)
    var d = {}
    for(var i=0; i<keys.length; i++) {
      var key = keys[i]
      d[key] = data[key]
    }
    Vue.set(state.slave_types, d['id'].toString(), d) 
  },
  SET_SLAVE(state, data) {
    var keys = Object.keys(data)
    var d = {}
    for(var i=0; i<keys.length; i++) {
      var key = keys[i]
      d[key] = data[key]
    }
    var slave_type = state.slave_types[d['slave_type_id'].toString()]
    var name = 'Slave-'+d['id']
    if (slave_type) {
      name = slave_type['name']
    }
    d['slave_type'] = name
    Vue.set(state.slaves_map, d['id'].toString(), d) 
  }
}

export default {
  state: defaultState,
  mutations,
  actions,
  getters
}
