const defaultState = {
  is_connected: false,
  last_update: null
}

const getters = {
  is_connected (state) {
    return state.is_connected
  },
  last_update (state) {
    return state.last_update
  }
}

const mutations = {
  SOCKET_CONNECT(state) {
    state.is_connected = true;
  },
  SOCKET_DISCONNECT(state) {
    state.is_connected = false;
  },
  last_update(state, value) {
    state.last_update = value
  }
}
export default {
  state: defaultState,
  getters,
  mutations
}
