import Vue from 'vue'
import Vuex from 'vuex'
import tasks_store from './modules/tasks_store'
import schedulers_store from './modules/schedulers_store'
import session_store from './modules/session_store'
import bots_store from './modules/bots_store'
import alerts_store from './modules/alerts_store'
import dataview_store from './modules/dataview_store'

Vue.use(Vuex)

export default new Vuex.Store({
  modules: {
    schedulers: schedulers_store,
    tasks: tasks_store,
    bots: bots_store,
    session: session_store,
    alerts: alerts_store,
    dataview: dataview_store
  }
})
