import Vue from 'vue'
import moment from 'moment'

function mutationLoop (context, mutation, data) {
  for (var i=0; i<data.length; i++) {
    context.commit(mutation, data[i])
  }
}
const defaultState = {
  alerts_map: {},
  alert_active: false,
  alert_msg: ''
}

const getters = {
  alert_ids (state, getters) {
    return Object.keys(state.alerts_map).map(id => parseInt(id))
  },
  alert_msg (state, getters) {
    return state.alert_msg
  },
  alert_active (state, getters) {
    return state.alert_active
  },
  unviewed_alerts (state, getters) {
    return getters.alerts.filter(function(alert_) {
      return alert_['viewed'] == false
    })
  },

  alerts: (state) => {
    return Object.keys(state.alerts_map).map(alert_id => state.alerts_map[alert_id])
  }
}
const actions = {
  SET_ALERT_ON: (context, data) => {
    var msg = ''
    var alerts = context.getters.unviewed_alerts
    var cnt = alerts.length
    console.log("alert on: " + cnt)
    if (cnt > 1) {
      msg = alerts[cnt-1].msg 
      msg+= ' + '+(cnt-1)+' more alerts'

    }

    
    else if (cnt == 1) {
      msg = alerts[0].msg
    }
    else
      msg = 'No alerts'

    if (cnt) {
      var tmp = moment.utc(alerts[cnt-1].time, 'yyyy-MM-DD HH:mm:ss')
      var time = new Date(tmp).getTime()
      var now = new Date()
      var offset = ( now.getTime()-time)/60000

      console.log('\n\n\n\n')
      if (offset) {
        if (offset < 1) offset = 'now'
        else offset = offset.toFixed(0) + ' mins ago'
        msg +=' ('+offset+')'
      }

      context.commit('SET_ALERT_MSG', msg)
      context.commit("SET_ALERT_ACTIVE", false)
      context.commit("SET_ALERT_ACTIVE", true)
    }
  },
  SET_ALERT_MSG: (context,  data) => {
    context.commit("SET_ALERT_MSG",data)
  },
  SET_ALERT: (context, data) => {
    context.commit("SET_ALERT", data)
    context.dispatch("SET_ALERT_ON", true)
  },
  SET_ALERTS: (context, data) => {
    mutationLoop(context, 'SET_ALERT', data)
    context.dispatch("SET_ALERT_ON", true)
  },
  RESET_ALERT_STORE: (context, data) => {
    context.commit("RESET_ALERT_STORE", data)
  }
}

const mutations = {
  SET_ALERT_ACTIVE (state, data) {
    state.alert_active = data
  },
  alert_active (state, data) {
    state.alert_active = data
  },

  SET_ALERT_MSG (state, data) {
    state.alert_msg = data
  },
  RESET_ALERT_STORE (state, data) {
    state.alerts_map = {} 
    state.alert_active = false
    state.alert_msg = ''
  },
  SET_ALERT (state, data) {
    var keys = Object.keys(data)
    var d = {}
    for(var i=0; i<keys.length; i++) {
      var key = keys[i]
      d[key] = data[key]
    }
    Vue.set(state.alerts_map, d['id'].toString(), d) 
  }
}

export default {
  state: defaultState,
  mutations,
  actions,
  getters
}
