<template>
</template>
<script>

import Vue from 'vue'


export default {
  data: () => {
    return {
    }
  },
  sockets: {
    connect(state) {
      this.$store.commit("SOCKET_CONNECT")
      console.log("Connected!")

      this.$store.dispatch("SET_ALERT", {'id': 0, 'msg': 'Connected!', 'viewed': false, 'go_to': '/alerts#alert_0', 'time': new Date()}) 
      this.$store.dispatch("SET_ALERT_ON") 

      var s = this.$socket.emit('pinger', {})
      console.log("PING: " + s.connected)
    },
    disconnect(state) {
      console.log("DISConnected!")

      this.$store.commit("SOCKET_DISCONNECT")
    },

    consoleLog(data) {
      console.log(data)
    },
    data_in(data) {
      var last_last_update = this.$store.getters.last_update
      var last_update = data['last_update']
      if (!last_last_update) {
        this.$store.dispatch(data['channel'], data['action_data'])
        this.$store.commit('last_update', last_update)
      }

      if (last_update - last_last_update == 0 ) { //same update
        console.log("Same update... ["+data['channel']+"]")
      }
      else if (last_update - last_last_update < 0)  { //old update
        console.log("Old update... ["+data['channel']+"]")
      }
      else if (last_update - last_last_update == 1) { //next update
        console.log("Next update ["+data['channel']+"]")
        this.$store.dispatch(data['channel'], data['action_data'])
        this.$store.commit('last_update', data['last_update'])

      }
      else if (last_update - last_last_update > 1) { //missed some updates
        console.log("Missed some updates...")
        this.$store.dispatch("RESET_TASK_STORE", {})
        this.$store.dispatch("RESET_BOT_STORE", {})
        this.$store.dispatch("RESET_SCHEDULER_STORE", {})
        this.$store.dispatch("RESET_ALERT_STORE", {})
        this.$socket.emit('getall', {})
      }
      this.$store.dispatch(data['channel'], data['action_data'])
      this.$store.commit('last_update', data['last_update'])
    },
    ponger(data) {
      console.log("Pong")
      var now = data['last_update']
      var last = this.$store.getters.last_update
      console.log("LAST_UPDATE: " + now + '==' + last)
      if (now != last) {
        console.log("updating...")
        this.$store.dispatch("RESET_TASK_STORE", {})
        this.$store.dispatch("RESET_BOT_STORE", {})
        this.$store.dispatch("RESET_SCHEDULER_STORE", {})
        this.$socket.emit('getall', {})
      }
      else {
        console.log("up to date")
      }
    }
  }
}
</script>

