<template>
  <div>
    <v-snackbar
      v-model="alert_active"
      :timeout="5000"
      top
      color="yellow darken-3"
    >
      <span class='font-weight-bold' style='color:black;'>
      {{$store.getters.alert_msg}}
      </span>
      <v-btn block dark :to='recentAlertLink'>
        Open
      </v-btn>
    
    </v-snackbar>

    <v-snackbar
      v-model="!$store.getters.is_connected"
      :timeout="0"
      top
      color="error"
    >
    Not connected to server...
    </v-snackbar>
  <v-card>
    <v-card-title>
      <h1>
        BN1 Command Portal 
      </h1>
    <v-spacer></v-spacer>
    <v-btn dark to="/logout">
      <span>Logout</span>
      <v-icon medium>how_to_reg</v-icon>
    </v-btn>

    </v-card-title>
    <v-bottom-nav
      :active.sync="bottomNav"
      :color="color"
      :value="true"
      dark
      shift
    >
      <v-btn dark to="/">
        <span>Overview</span>
        <v-icon medium>dashboard</v-icon>
      </v-btn>
      <v-btn dark to="/jobs">
        <span>Jobs</span>
        <v-icon medium>work</v-icon>
      </v-btn>

      <v-btn dark to="/data">
        <span>Data</span>
        <v-icon medium>timeline</v-icon>
      </v-btn>


      <v-btn dark to="/bots">
        <span>Bots</span>
        <v-icon medium>adb</v-icon>
      </v-btn>

      <v-btn dark to="/settings">
        <span>Settings</span>
        <v-icon>settings</v-icon>
      </v-btn>
    </v-bottom-nav>
  </v-card>
  </div>
</template>
<script>
  export default {
    data () {
      return {
        bottomNav: 3
      }
    },

    computed: {
      alert_active: {
        get () {
          return this.$store.getters.alert_active
        },
        set(value) {
          if (value==true)
            this.$store.dispatch("SET_ALERT_ON")
          this.$store.commit('SET_ALERT_ACTIVE', value)
        }
      },
      recentAlertLink: {
        get () {
          if (this.$store.getters.unviewed_alerts.length)
              return "alerts#alert_"+this.$store.getters.unviewed_alerts[0]["id"]
          return 'alerts'
        }
      },
      color () {
        switch (this.bottomNav) {
          case 0: return 'indigo'
          case 1: return 'green darken-1'
          case 2: return 'orange'
          case 3: return 'purple lighten-1'
          case 4: return 'grey darken-2'
        }
      }
    }
  }
</script>
