import Vue from 'vue'
function mutationLoop (context, mutation, data) {
  for (var i=0; i<data.length; i++) {
    context.commit(mutation, data[i])
  }
}


const defaultState = {
  table_selections: {'RCB': 'RCBs'},
  table_headers: [{'text': 'Id', value: 'id'}, {'text': 'Name', value: 'name'}, {'text': 'Amount ($)', value: 'amount'}],
  ctable: null,
  name: null,
  page: null,
  rows_map: {asd: {id:'asd', name:'Joe', amount: 500}}
}


const getters = {
  table_headers: (state) => {
    return state.table_headers
  },
  table_page: (state) => {
    return state.page
  },
  table_names: (state) => {
    return Object.keys(state.table_selections).map(function(key) {
      return state.table_selections[key]
    })
  },
  rows: (state) => {
    return Object.keys(state.rows_map).map(id => state.rows_map[id])
  },
  rows_map: (state) => {
    return state.rows_map
  },


}

const actions = {

  SET_TABLE_PAGE: (context, data) => {
    context.commit("SET_TABLE_PAGE", data)
  },
  SET_CTABLE: (context, data) => {
    context.commit('SET_CTABLE', data) 
  },
  SET_ROW: (context, data) => {
    context.commit('SET_ROW', data)

  },
  SET_ROWS: (context, data) => {
    mutationLoop(context, 'SET_ROWS', data)
  }
}

const mutations = {
  SET_TABLE_PAGE(state, page) {
    state.page = page
  },
  SET_CTABLE(state, data) {
    var keys = Object.keys(data)
    var d = {}
    for(var i=0; i<keys.length; i++) {
      var key = keys[i]
      d[key] = data[key]
    }
  },
  SET_ROW(state, data) {
    var keys = Object.keys(data)
    var d = {}
    for(var i=0; i<keys.length; i++) {
      var key = keys[i]
      d[key] = data[key]
    }
    Vue.set(state.rows_map, d['id'].toString(), d) 
  }
}

export default {
  state: defaultState,
  mutations,
  actions,
  getters
}

