import Vue from 'vue'
import moment from 'moment'


function mutationLoop (context, mutation, data) {
  for (var i=0; i<data.length; i++) {
    context.commit(mutation, data[i])
  }
}


const defaultState = {
  scheduler_groups_map: {},
  schedulers_map: {},
}

const getters = {
  schedulers_map (state, getters) {
    return state.schedulers_map
  },
  schedules (state, getters) {
      var groups = getters.scheduler_groups
      var schedulers = getters.schedulers
      var rows = []
      
      if (!(groups && schedulers)) return rows

      var day_map = ['Sun', 'Mon', 'Tue', 'Wed', 'Thu', 'Fri', 'Sat'] 

      for (var i=0; i<groups.length; i++) {
        var row = {'cnt':0, 'repeat': false}
        var run_time = ''
        var run_days = ''
        var time = ''
        var next_run = null
        for (var j=0; j<schedulers.length; j++) {
          if (schedulers[j].scheduler_group_id == groups[i].id) {
            var d = moment.utc(schedulers[j].run_time, 'yyyy-MM-DD HH:mm:ss')
            console.log(d)
            d = new Date(d)
            //var d = new Date(schedulers[j].run_time)
            console.log(d)
            console.log('------------\n\n\n')

            row['cnt']+=1

            if (schedulers[j].frequency_min) {
              row['repeat'] = true
              row['frequency'] = schedulers[j].frequency_min
              run_days += day_map[d.getDay()] + ', '
            }

            else {
              row['repeat'] = false
              row['frequency'] = 'Non repeating'
            }

            console.log(schedulers[j].run_time)

            var now = new Date()
            if (!next_run) {
              next_run = d
            }
            else if (d - now >=0 && (d - now) < (next_run - now)){
              next_run = d
            }
          }
        }
        if (row['cnt'] > 0) { //if has schedulers in group 
          run_time=day_map[next_run.getDay()]
          var x =next_run.getMilliseconds() - next_run.getTimezoneOffset() * 60000 //changes to local timezone
          next_run.setMilliseconds(x)

          var hours = next_run.getHours().toString() 
          var minutes = next_run.getMinutes().toString()
          if (minutes.length == 1) minutes = '0'+ minutes
          if (hours.length == 1) hours = '0'+hours
          time = hours + ':' + minutes

          row['run_time'] = run_time + '@' +time 
          if (row['repeat']) {
            if (row['cnt'] == 1) {
              var freq = row['frequency']
              var unit = 'minutes'
              row['frequency'] = 'Every ' + freq +' '+unit 
            }
            else if (row['cnt'] > 1) {
              row['frequency'] = run_days.substr(0, run_days.length-2)
            }
          }

          row['group_id'] = groups[i].id
          row['name'] = groups[i].name
          rows.push(row)
        }
      }
    console.log("ROOWSSS: "+ JSON.stringify(rows))
    return rows
  },

  schedulers: (state) => {
    return Object.keys(state.schedulers_map).map(id => state.schedulers_map[id])
  },
  scheduler_groups: (state) => {
    return Object.keys(state.scheduler_groups_map).map(id => state.scheduler_groups_map[id])

  }
}

const actions = {
  RESET_SCHEDULER_STORE: (context, data) => {
    context.commit("RESET_SCHEDULER_STORE", data)
  },
  SET_SCHEDULER_GROUP: (context, data) => {
    context.commit("SET_SCHEDULER_GROUP", data)
  },
  SET_SCHEDULER_GROUPS: (context, data) => {
    mutationLoop(context, 'SET_SCHEDULER_GROUP', data)
  },
  SET_SCHEDULER: (context, data) => {
    context.commit('SET_SCHEDULER', data)
  },
  SET_SCHEDULERS: (context, data) => {
    mutationLoop(context, 'SET_SCHEDULER', data)
  }
}

const mutations = {
  RESET_SCHEDULER_STORE (state, data) {
    state.schedulers_map = {}
    state.scheduler_groups_map = {}
  },

  SET_SCHEDULER_GROUP(state, data) {
    var keys = Object.keys(data)
    var d = {}
    for(var i=0; i<keys.length; i++) {
      var key = keys[i]
      d[key] = data[key]
    }
    Vue.set(state.scheduler_groups_map, d['id'].toString(), d)
  },

  SET_SCHEDULER(state, data) {
    var keys = Object.keys(data)
    var d = {}
    for(var i=0; i<keys.length; i++) {
      var key = keys[i]
      d[key] = data[key]
    }
    Vue.set(state.schedulers_map, d['id'].toString(), d) 
  }
}

export default {
  state: defaultState,
  mutations,
  actions,
  getters
}
