import Vue from 'vue'
import './plugins/vuetify'
import App from './App.vue'
import router from './router'
import Vuex from 'vuex'
import socketio from 'socket.io-client'
import VueSocketIO from 'vue-socket.io'
import VueMoment from 'vue-moment'
import store from './store'

import { library } from '@fortawesome/fontawesome-svg-core'
import { faCircleNotch } from '@fortawesome/free-solid-svg-icons'
import { FontAwesomeIcon } from '@fortawesome/vue-fontawesome'

library.add(faCircleNotch)

Vue.component('font-awesome-icon', FontAwesomeIcon)
var host = `//${window.location.hostname}` //+ ':80'
export const socket_instance = socketio(host);
//export const socket_instance = socketio(`//${window.location.origin}`)
Vue.use(VueSocketIO, socket_instance, store)
Vue.use(Vuex)
Vue.use(require('vue-moment'));


//export const SocketInstance = socketio(`//${window.location.host}`);
//Vue.use(VueSocketIO, SocketInstance, store)



import 'vuetify/dist/vuetify.min.css'
Vue.config.productionTip = false
Vue.config.devtools = true;

new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app')

