import Vue from 'vue'
import moment from 'moment'

function mutationLoop (context, mutation, data) {
  for (var i=0; i<data.length; i++) {
    context.commit(mutation, data[i])
  }
}


const defaultState = {
  cjob_id: null,
  job_filter: null,
  task_groups_map: {},
  tasks_map: {},
  jobs_map: {},
}

const getters = {
  filtered_jobs (state, getters) {
    if (state.job_filter == -1) return getters.job_queued
    else if (state.job_filter == 0) return getters.job_inprogress
    else if (state.job_filter == 1) return getters.job_completed
    else if (state.job_filter == 2) return getters.job_errors
    else return getters.jobs
  },
  job_queued (state, getters) {
    return getters.jobs.filter(function(job) {
      if (job['stage'] == 'Queued') {
        return true
      }
    })
  },
  job_errors (state, getters) {
    return getters.jobs.filter(function(job) {
      if (job['status'] == 2) {
        return true
      }
    })
  },
  job_completed (state, getters) {
    return getters.jobs.filter(function(job) {
      if (job['status']==1) {
        return true
      }
    })
  },
  job_inprogress(state, getters) {
    return getters.jobs.filter(function(job) {
      if (job['status'] == 0 && job['stage'] != 'Queued') {
        return true
      }
    })
  },
  task_errors (state, getters) {
    return getters.tasks.filter(function(task) {
      if (task['status']==2) {
        return true
      }
    });
  },
  task_completed (state, getters) {
    return getters.tasks.filter(function(task) {
      if (task['status']==1) {
        return true
      }
    });

  },
  task_inprogress (state, getters) {
    return getters.tasks.filter(function(task) {
      console.log(JSON.stringify(task))
      if (task['status']==0) {
        return true
      }
    });
  },
  cjob_id (state) {
    return state.cjob_id
  },
  job_tasks (state, getters)  {
    var found_group_ids = []
    console.log('cjob_id: ' +  state.cjob_id)

    return getters.tasks.filter(function(task) {
      if (task['job_id'] == state.cjob_id) {
        if (task['task_group_id']) {
          if (!task['id'].toString().includes('tg')) {
            return false
          }
        }
        /*
        var group_id = task['task_group_id']

        if (group_id) {
          if (found_group_ids.includes(group_id)){
            console.log('is already in')
            return false
          }
          found_group_ids.push(group_id)
          task['name'] =  "Group Task: " + task['name']
        }
*/
        return true
      }
    });
  },
  jobs: (state) => {
    return Object.keys(state.jobs_map).map(job_id => state.jobs_map[job_id])
  },

  tasks: (state) => {
    return Object.keys(state.tasks_map).map(task_id => state.tasks_map[task_id])
  },

  tasks_map: (state) => {
    return state.tasks_map
  },
  jobs_map: (state) => {
    return state.jobs_map
  },
  task_groups: (state) => {
    return Object.keys(state.task_groups_map).map(group_id => state.task_groups_map[group_id])
  },
  task_groups_map: (state) => {
    return state.task_groups_map
  },
  task_group_ids: (state, getters) => {
    var task_groups = getters.task_groups
    var group_ids = []
    for (var i=0; i<task_groups.length; i++) {
      group_ids.push(task_groups[i]['id'])
    }
    return group_ids
  }
}

const actions = {
  RESET_TASK_STORE: (context, data) => {
    console.log("RESET_TASK_STORE")
    context.commit("RESET_TASK_STORE", data)
  },
  SET_TASK_GROUP: (context, data) => {
    context.commit("SET_TASK_GROUP", data)
  },
  SET_TASK_GROUPS: (context, data) => {
    mutationLoop(context, 'SET_TASK_GROUP', data)
  },
  SET_JOB: (context, data) => {
    context.commit('SET_JOB', data)

  },
  SET_JOBS: (context, data) => {
    mutationLoop(context, 'SET_JOB', data)
  },

  SET_TASK: (context, data) => {
    context.commit("SET_TASK", data)
  },
  SET_TASKS: (context, data) => {
    mutationLoop(context, 'SET_TASK', data)
  }

}

const mutations = {
  RESET_TASK_STORE (state, data) {
    state.tasks_map = {}
    state.task_groups_map = {}
    state.jobs_map = {}
    state.cjob_id = null
  },
  SET_CJOB_ID(state, job_id) {
    state.cjob_id = job_id
  },
  SET_JOB_FILTER(state, status_filter) {
    state.job_filter = status_filter
  },
  SET_TASK_GROUP(state, data) {
    var keys = Object.keys(data)
    var d = {}
    for(var i=0; i<keys.length; i++) {
      var key = keys[i]
      d[key] = data[key]
    }
    var tg_id = 'tg-'+data['id']
    var td = {}
    if (!Object.keys(state.task_groups_map).includes(d['id'].toString())) {
      var td = {'id':tg_id, 'name': "TG_", 'job_id': data['job_id']}
    }
    else {
      td = state.tasks_map[tg_id]
      var info = "["+d["completed_cnt"]+"/"+d['total_cnt']+"]"
      var name = td['name']
      var idx = name.indexOf('[')
      if (idx==-1) idx = name.length
      name = name.substring(0, idx)
      td['name'] = name + " "  + info
    }

    Vue.set(state.tasks_map, tg_id, td)
    Vue.set(state.task_groups_map, d['id'].toString(), d)
  },

  SET_TASK(state, data) {
    var keys = Object.keys(data)
    var d = {}
    for(var i=0; i<keys.length; i++) {
      var key = keys[i]
      d[key] = data[key]
    }
    
    var ts = 'N/A'
    var tc = 'N/A'
    console.log('ts')
    console.log(d['time_started'])


    if (d['time_started']) {
      var d1 = moment.utc(d['time_started'], 'yyyy-MM-DD HH:mm:ss')
      d1 = new Date(d1)

      var hours = d1.getHours()
      var mins = d1.getMinutes()
      var ampm = (hours >= 12) ? "PM" : "AM";
      hours = (hours<10) ? '0': '' + hours.toString()
      mins = (mins<10) ? '0': '' + mins.toString()

      ts = hours+':'+mins+ampm

      if (d['time_completed']) {
        d1 = moment.utc(d['time_completed'], 'yyyy-MM-DD HH:mm:ss')
        d1 = new Date(d1)

        hours = d1.getHours()
        mins = d1.getMinutes()
        ampm = (hours >= 12) ? "PM" : "AM";

        hours = (hours<10) ? '0': '' + hours.toString()
        mins = (mins<10) ? '0': '' + mins.toString()

        tc = hours+':'+mins+ampm
      }
    }

    d['time_started'] = ts
    d['time_completed'] = tc

    if (d['task_group_id']) {
      var group_id = d['task_group_id'].toString()
      if (Object.keys(state.task_groups_map).includes(group_id)) {
        var task_id = 'tg-'+group_id
        var task = state.tasks_map[task_id] 
        if (task) {
          if (task['name'].includes("TG_")) {
            task['name'] = d['name']
            task['time_started'] = ts
            task['time_completed'] = 'N/A'
          }

          Vue.set(state.tasks_map, task_id, task)
          var group = state.task_groups_map[group_id]

          if (group['completed_cnt']==group['total_cnt']) {
            task['status'] = 1
            task['time_completed'] = tc
          }
          else if (group['completed_cnt']!= group['total_cnt'] && group['error_cnt']) {
            task['status'] = 3
          }
          else {
            task['status'] = 0
          }
          Vue.set(state.tasks_map, task_id, task)
        }
      }
    }


    if (d['error'] == true) {
        d['status'] = 2
      if (d['completed'])
        d['status'] = 3 // error because of another task error
    }
    else
      d['status'] = d['completed'] ? 1:0
    Vue.set(state.tasks_map, d['id'].toString(), d) 
  },
  SET_JOB(state, data) {
    //state.jobs_map[data['id']] = data

    var keys = Object.keys(data)
    var d = {}
    for(var i=0; i<keys.length; i++) {
      var key = keys[i]
      d[key] = data[key]
    }
    if (d['error'] == true)
      d['status'] = 2
    else
      d['status'] = d['completed'] ? 1:0

    Vue.set(state.jobs_map, d['id'].toString(), d) 
  }
}

export default {
  state: defaultState,
  mutations,
  actions,
  getters
}
